import React from "react";
import styled from "styled-components";
import Particle from "../Components/Particle";
import FacebookIcon from "@material-ui/icons/Facebook";
import GitHubIcon from "@material-ui/icons/GitHub";
import YoutubeIcon from "@material-ui/icons/YouTube";

function HomePage() {
  return (
    <HomePageStyled>
      <div className="particle-icon">
        <Particle />
      </div>
      <div className="typography">
        <h1>Hi! I'm Tuyen <span>Lorem Ipsum</span></h1>
        <p>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
          Nisl tincidunt eget nullam non. Quis hendrerit dolor magna eget est lorem ipsum dolor sit. Volutpat odio facilisis mauris sit amet massa.
          Commodo odio aenean sed adipiscing diam donec adipiscing tristique. Mi eget mauris pharetra et. Non tellus orci ac auctor augue.
        </p>

        <div className="icons">
          <a href="/" className="icon i-facebook">
            <FacebookIcon />
          </a>
          <a href="/" className="icon i-github">
            <GitHubIcon />
          </a>
          <a href="/" className="icon i-youtube">
            <YoutubeIcon />
          </a>
        </div>
      </div>
    </HomePageStyled>
  )
}

const HomePageStyled = styled.header`
  witdh: 100%;
  height: 100vh;
  position: relative;
  
  .typography{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
    text-align: center;
    width: 80%;
    
    .icons{
      display: flex;
      justify-content: center;
      margin-top: 1rem;
      .icon{
        border: 2px solid var(--border-color);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        transition: all .4s ease-in-out;
        cursor: pointer;
        &:hover{
          border: 2px solid var(--border-color);
          color: var(--primary-color)
        }
        &:not(:last-child){
          margin-right: 1rem;
        }
        svg{
          margin: .5rem;
        }
      }
      
      .i-youtube{
        &:hover{
          border: 2px solid red;
          color: red;
        }
      }
      
      .i-github{
        &:hover{
          border: 2px solid #5F4687;
          color: #5F4687;
        }
      }
    }
  }
`;

export default HomePage;